/* globals console, window, document */

const templateDataSets = [
	{
		id: '78900',
		label: 'David Lee',
		values: {
			guestName: 'David Lee',
			reservationNumber: 'Y2JKH5G1Z',
			guestArrivalDate: new Date( 2024, 7, 22 ).toLocaleDateString(),
			guestDepartureDate: new Date( 2024, 8, 22 ).toLocaleDateString(),
			numberOfGuests: 2,
			roomType: 'Double Room',
			guestPhoneNumber: '567-890-1234'
		}
	},
	{
		id: '78901',
		label: 'Kate Smith',
		values: {
			guestTitle: 'Ms.',
			guestName: 'Kate Smith',
			reservationNumber: 'GRJKCCG23',
			guestArrivalDate: new Date( 2024, 4, 12 ).toLocaleDateString(),
			guestDepartureDate: new Date( 2024, 9, 22 ).toLocaleDateString(),
			numberOfGuests: 3,
			roomType: 'Apartment',
			guestPhoneNumber: '557-333-1234',
			guestDiscount: '30%'
		}
	}
];

const additionalMergeFieldsConfig = {
	previewModes: [ '$dataSets' ],
	initialPreviewMode: templateDataSets[ 0 ].id,
	dataSets: templateDataSets
};

const templateConfig = {
	definitions: [
		{
			title: 'Discount information',
			description: 'Simple footer with discount information',
			data: '<p><em>P.S. Mention this email to receive {{guestDiscount}} off your first spa treatment!</em></p>'
		}
	]
};

window.ClassicEditor
	.create( document.querySelector( '#snippet-merge-fields-template' ), {
		mergeFields: { ...window.ClassicEditor.defaultConfig.mergeFields, ...additionalMergeFieldsConfig },
		toolbar: {
			items: [
				'insertMergeField', 'previewMergeFields',
				'|', 'insertTemplate'
			].concat( window.ClassicEditor.defaultConfig.toolbar.items )
		},
		template: templateConfig
	} )
	.then( editor => {
		window.editor = editor;

		// Prevent showing a warning notification when user is pasting a content from MS Word or Google Docs.
		window.preventPasteFromOfficeNotification = true;

		window.attachTourBalloon( {
			target: window.findToolbarItem(
				editor.ui.view.toolbar, item => item.buttonView && item.buttonView.label === 'Insert template'
			),
			text: 'Click to add a template containing a merge field.',
			editor,
			tippyOptions: {
				placement: 'bottom-start'
			}
		} );
	} )
	.catch( err => {
		console.error( err.stack );
	} );
